import * as React from "react";
import ImageList from "@mui/material/ImageList";
import ImageListItem from "@mui/material/ImageListItem";
import ImageListItemBar from "@mui/material/ImageListItemBar";
import IconButton from "@mui/material/IconButton";
import StarBorderIcon from "@mui/icons-material/StarBorder";

function srcset(image, width, height, rows = 1, cols = 1) {
  return {
    src: `${image}?w=${width * cols}&h=${height * rows}&fit=crop&auto=format`,
    srcSet: `${image}?w=${width * cols}&h=${
      height * rows
    }&fit=crop&auto=format&dpr=2 2x`
  };
}

export default function CustomImageList() {
  return (
    <ImageList
      sx={{
        // Promote the list into its own layer in Chrome. This costs memory, but helps keeping high FPS.
        transform: "translateZ(0)"
      }}
      gap={1}
    >
      {itemData.map((item) => {
        const cols = item.featured ? 2 : 1;
        const rows = item.featured ? 2 : 1;

        return (
          <ImageListItem key={item.img} cols={cols} rows={rows}>
            <img
              {...srcset(item.img, 250, 200, rows, cols)}
              alt={item.title}
              loading="lazy"
            />
            <ImageListItemBar
              sx={{
                background:
                  "linear-gradient(to bottom, rgba(0,0,0,0.7) 0%, " +
                  "rgba(0,0,0,0.3) 70%, rgba(0,0,0,0) 100%)"
              }}
              title={item.title}
              position="top"
              actionIcon={
                <IconButton
                  sx={{ color: "white" }}
                  aria-label={`star ${item.title}`}
                >
                  <StarBorderIcon />
                </IconButton>
              }
              actionPosition="left"
            />
          </ImageListItem>
        );
      })}
    </ImageList>
  );
}

const itemData = [
  {
    img:
      "https://drive.google.com/uc?export=view&id=1I3bptR2kucF5mWhCQ5O8MYvkZkdiPT-_&usp=sharing",
    featured: true
  },
  {
    img:
      "https://drive.google.com/uc?export=view&id=1sIAH5unip3c_JhKg-GIUhC7-ceZ-gVRu&usp=sharing"
  },
  {
    img:
      "https://drive.google.com/uc?export=view&id=1VO1poU0CWy6cQYdXPznc43wJ5-ppLCd_&usp=sharing"
  },
  {
    img:
      "https://drive.google.com/uc?export=view&id=1mQi1m4vbjmig0IXyWxKV63zQ7cH1GNtd&usp=sharing",
    featured: true
  }
];
